@import '../base';

.projects {
  max-width: 100%;
  padding-bottom: 100px;

  &-content {
    .projects-title {
      margin: 45px 0 30px 200px;
      color: $darkgray;
      opacity: 0;

      &.fadeIn {
        animation: fadeIn linear 0.8s forwards;
      }
    }

    .projects-list {
      width: calc(100vw - (200px * 2));
      margin: 0px auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 40px;

      /* PROJECT ITEM */
      .project {
        display: flex;
        flex-direction: column;
        opacity: 0;

        &.fadeIn {
          animation: fadeIn linear 0.8s forwards;
          animation-delay: 0.1s;
        }

        /* PROJECT IMAGE */
        &-img {
          & a {
            height: 100%;
            width: 100%;
            display: inline-block;

            &:hover {
              overflow: hidden;
            }
          }

          & img {
            height: 100%;
            width: 100%;

            &:hover {
              transform: scale(1.1);
              transition: all 0.3s ease;
              vertical-align: middle;
            }
          }
        }

        /* DESCRIPTION TEXT */
        &-description {
          display: flex;
          flex-direction: column;
          margin-top: 10px;

          .project-title-section {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: auto;
            width: 100%;
            line-height: 1.8;

            .project-maintitle {
              font-size: $fontsize-largeText;
              font-weight: 900;
              color: $darkgray;
            }

            .project-subtitle {
              font-size: $fontsize-minText;
            }
          }

          .project-tech {
            line-height: 1.8;
            font-size: $fontsize-smallText;
          }

          .project-link {
            padding: 10px 0;

            a {
              padding: 5px 10px;
              margin-right: 10px;
              line-height: 1.8;
              border-radius: 5px;
              font-size: $fontsize-smallText;
              text-decoration: none;
              background-color: darken($lightgray, 10%);

              &:hover {
                cursor: pointer;
                background-color: $royalblue;
                color: white;
              }

              &:visited {
                text-decoration: none;
                color: $gray;

                &:hover {
                  cursor: pointer;
                  background-color: $royalblue;
                  color: white;
                }
              }
            }
          }
        }
      }
    }
  }

  &-content.dark {
    .projects-title {
      color: white;
    }

    .projects-list {
      .project {
        &-description {
          .project-title-section {
            .project-maintitle {
              color: white;
            }
          }

          .project-link {
            a {
              color: $darkgray;

              &:hover {
                background-color: $lightblue;
                color: $darkgray;
              }

              &:visited {
                &:hover {
                  background-color: $lightblue;
                  color: $darkgray;
                }
              }
            }
          }
        }
      }
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(5%);
  }

  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 767px) {
  .projects {
    display: flex;
    justify-content: center;

    &-content {
      width: 90vw;

      .projects-title {
        margin: 45px 0 30px 0px;
      }

      .projects-list {
        width: 100%;
        grid-template-columns: 1fr;
      }
    }
  }
}
